.app__header{
  height:75px;
  width:100%;
  z-index:100;
  position:fixed;
  top:0;
  left:0;
  display:flex;
  justify-content:space-around;
  color:#82ae46;
}

.app__header__shadow{
-webkit-box-shadow: 0px 0px 9px 3px rgba(41,41,41,.25);
-moz-box-shadow: 0px 0px 9px 3px rgba(41,41,41,.25);
box-shadow: 0px 0px 9px 3px rgba(41,41,41,.25);
}

.app__header__logo{
  align-self:center;
  font-size:1.8rem;
  font-weight: bolder;
}
.app__header__buttons{
  width:400px;
  display:flex;
  justify-content:flex-end;
  align-self:center;
  font-size:1.2rem;
}

.home__content{
  position:relative;
}
